@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap);
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #FCF6F4;
    --color-bg-varient: #d8d8d8;
    --color-primary: black;
    --color-primary-variant: #2b2727;
    --color-white: #fff;
    --color-black: black;
    --color-light: rgb(87, 78, 78);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Quicksand', sans-serif;
    background-color: #FCF6F4;
    background-color: var(--color-bg);
    color: black;
    color: var(--color-black);
    line-height: 1.7;
}

/* general styles */
.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 6rem;
}

section > h2, section > h5{
    text-align: center;
    color: rgb(87, 78, 78);
    color: var(--color-light);
}

.text-light{
    color: rgb(87, 78, 78);
    color: var(--color-light);
}

a{
    color: black;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

/* a:hover{
    color: var(--color-white);
} */

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color:  black;
    color:  var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid black;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #FCF6F4;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: black;
    background: var(--color-primary);
    color: #FCF6F4;
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* media query (medium devices) */
@media screen and (max-width: 1024px){
    .container{
        width:86%;
        width:var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/* media query (small devices) */
@media  screen and (max-width: 600px){
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}
    


.aHome{
    position: absolute;
    right:50% ;
    top: calc(1rem + 0.8vw);
    text-decoration: none;
    z-index: 1;
}

.about__box{
    position: absolute;
    top: 15%;
    left: 22.5%;
    

    width: 55vw;
    /* height: 55vh; */
    display: flex;
    flex-direction: column;

    border-left: 2px solid var(--color-black);
    border-right: 2px solid var(--color-black);
    border-top: 2px solid var(--color-black);
    border-bottom: 2px solid var(--color-black);

    background-color: var(--color-bg);
}

.text{
    justify-content: space-evenly;
    padding: 1rem;
}






.Logo{
    display: inline-block;
    font-family: 'Pacifico', cursive;
    color: var(--color-black);
    font-size: 1.8rem;
    font-weight: bold;
    position: fixed;
    left: 1rem;
    top: 1rem;
    z-index: 3;
}
.main__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    
    grid-gap: 0.8rem;
    
    gap: 0.8rem;

    position: fixed;
    margin-top: 2rem;
    bottom: 0;
    left: 2rem;
    z-index: 3;
}

.main__socials::after{
    content: '';
    width: 2px;
    height: 9rem;
    background: var(--color-primary);
}
.eHome{
    position: absolute;
    top: 50%;
    right: calc(1rem + 2vw);
    -webkit-transform: rotate(90deg) translate(-50%, -50%);
            transform: rotate(90deg) translate(-50%, -50%);
    text-decoration: none;
    z-index: 1;
}

.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem;
}

.experience__container > div{
    background: var(--color-bg-varient);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 1.5rem;
    color: var(--color-primary);
}

.experience__container > div h4{
    text-align: center;
    margin-bottom: 1rem;
}

.experience__content{
    /* display: grid;
    grid-template-columns: 1fr;
    row-gap: 2rem; */
}

.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
}


/* media query med */

@media screen and (max-width: 1024px){
    .experience__container{
        grid-template-columns: 1fr;
    }

    .experience__container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
}
    
/* media query small */
@media screen and (max-width: 600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container > div{
        width: 75%;
        padding: 2rem 3rem; 
        margin: 0 auto;
        /* grid-template-columns: 2fr; */
    }
    
}
.pHome{
    position: absolute;
    top: 50%;
    left: calc(1rem + 2vw);
    -webkit-transform: translate(-50%, -50%) rotate(-90deg) ;
            transform: translate(-50%, -50%) rotate(-90deg) ;
    text-decoration: none;
    z-index: 1;
}

.portfolio__container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__items:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio__item-img{
    display: flex;
    border-radius: 1.5rem;
    overflow: hidden;
    width: auto;
    /* max-height: 340px; */
    height: 340px;
    background-color: black;
    align-items: center;
    
}

.portfolio__item h3{
margin: 1.2 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}


/* media query med */
@media screen and (max-width: 1024px){
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;

    }
}

/* media query small */
@media screen and (max-width: 600px){
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
        padding: 0rem 3.5rem;
    }
}
    

.cHome{
    position: absolute;
    right:48.25% ;
    top: calc(1rem + 2vw);
    text-decoration: none;
    z-index: 1;
}

#contact{
    margin-top: 7rem;
}

.container.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width:100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-black);
}

/* media query med */
@media screen and (max-width: 1024pxpx){
    .container.contact__container{
        grid-gap: 2rem;
        gap: 2rem;
        grid-template-columns: 1fr;
    }
}
    
/* media query small */
@media screen and (max-width: 600px){
    .container.contact__container{
        width: var(--container-width-sm);
    }
}
